  font-family: "PT Mono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/pt-mono_regular.woff2") format("woff2"), url("../fonts/pt-mono_regular.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-condensed_regular.woff2") format("woff2"), url("../fonts/roboto-condensed_regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-style: normal;
