html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #666666;
  background-color: #ffffff; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea {
  border-radius: 0; }

svg {
  fill: inherit; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%); }

.button {
  padding: 0;
  font: inherit;
  color: inherit;
  border: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  padding: 14px 0 13px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 5px;
  background: none;
  cursor: pointer;
  transition: all 0.3s ease-in 0s; }

.button:hover,
.button:focus {
  background: #ffffff;
  color: #00b2ff; }

.button:active {
  opacity: 0.7; }

.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 112px; }

.social-list__item-link {
  display: block;
  line-height: 0; }
  .social-list__item-link:hover .social-list__item-icon,
  .social-list__item-link:focus .social-list__item-icon {
    fill: #00b2ff; }

.social-list__item-icon {
  transition: all 0.3s ease-in 0s;
  fill: #cdcdcd; }

.page-header {
  width: 100%;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;
  background: black; }

.page-header__inner {
  max-width: 1480px;
  margin: 0 auto;
  padding: 0 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 33px;
  padding-bottom: 33px; }

.page-header__logo {
  transition: all 0.3s ease-in 0s; }
  .page-header__logo[href]:hover, .page-header__logo[href]:focus {
    opacity: 0.7; }
  .page-header__logo[href]:active {
    opacity: 0.3; }

.page-header__contacts {
  display: flex;
  align-items: center;
  flex-wrap: wrap; }

.page-header__link {
  display: flex;
  align-items: center;
  color: #ffffff;
  transition: all 0.3s ease-in 0s; }
  .page-header__link svg {
    fill: #00b2ff;
    transition: all 0.3s ease-in 0s; }
  .page-header__link[href]:hover, .page-header__link[href]:focus {
    color: #00b2ff; }
    .page-header__link[href]:hover svg, .page-header__link[href]:focus svg {
      fill: #ffffff; }
  .page-header__link[href]:active {
    opacity: 0.7; }

.page-header__link_email {
  margin-right: 88px; }

.page-header__link_email .page-header__link-icon {
  margin-right: 20px; }

.page-header__link_phone {
  margin-right: 50px; }

.page-header__link_phone .page-header__link-icon {
  margin-right: 16px; }

.page-header__callback-button {
  width: 180px; }

@media (max-width: 1023px) {
  .page-header {
    max-width: 1023px;
    margin: 0 auto;
    padding: 0 25px; }
  .page-header__inner {
    padding: 20px 0; }
  .page-header__link {
    margin-right: 0;
    margin-bottom: 3px; }
  .page-header__link-text {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden; }
  .page-header__link_email {
    margin-right: 34px; }
    .page-header__link_email .page-header__link-icon {
      width: 25px;
      height: 18px;
      margin-right: 0; }
  .page-header__link_phone .page-header__link-icon {
