    height: 24px;
    margin-right: 0; }
  .page-header__callback-button {
    display: none; } }

@media (max-width: 767px) {
  .page-header {
    max-width: 767px;
    margin: 0 auto;
    padding: 0 15px; }
  .page-header__inner {
    padding: 15px 0; }
  .page-header__logo {
    margin-top: 2px;
    margin-left: 2px; }
    .page-header__logo-image {
